import Vue from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";
import VueAnalytics from "vue-analytics";
import {
  Button,
  Layout,
  Icon,
  Menu,
  Card,
  Row,
  Col,
  Avatar,
  Table,
  Tag,
  Pagination,
  Divider,
  PageHeader,
  Tabs,
  Form,
  Modal,
  Input,
  Radio,
  Dropdown,
  FormModel,
  List,
  Transfer,
  Tooltip,
  DatePicker,
  Select,
  Alert,
  Switch,
  Spin,
  Message,
  Result,
  Slider,
  Drawer,
  Upload,
  Checkbox
} from "ant-design-vue";
import "./sass/main.scss";

Vue.config.productionTip = false;

Vue.use(Button);
Vue.use(Layout);
Vue.use(Icon);
Vue.use(Menu);
Vue.use(Card);
Vue.use(Row);
Vue.use(Col);
Vue.use(Avatar);
Vue.use(Table);
Vue.use(Tag);
Vue.use(Pagination);
Vue.use(Divider);
Vue.use(PageHeader);
Vue.use(Tabs);
Vue.use(Form);
Vue.use(Modal);
Vue.use(Input);
Vue.use(Radio);
Vue.use(Dropdown);
Vue.use(FormModel);
Vue.use(List);
Vue.use(Transfer);
Vue.use(Tooltip);
Vue.use(DatePicker);
Vue.use(Select);
Vue.use(Alert);
Vue.use(Switch);
Vue.use(Spin);
Vue.use(Message);
Vue.use(Result);
Vue.use(Slider);
Vue.use(Drawer);
Vue.use(Upload);
Vue.use(Checkbox);

Vue.use(VueAnalytics, {
  id: "UA-111257103-2",
  router
});

Vue.prototype.$message = Message; //全局提示
Vue.prototype.$modal = Modal; //对话框

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
