<template>
  <div id="app">
    <a-spin class="init-loding" v-if="loading">
      <a-icon slot="indicator" type="loading" />
    </a-spin>
    <BasicLayout v-if="!loading" />
  </div>
</template>

<script>
import BasicLayout from "./layout/BasicLayout";
import { mapActions, mapGetters } from "vuex";
export default {
  components: {
    BasicLayout
  },
  computed: {
    ...mapGetters(["loading"])
  },
  mounted() {
    this.initDate(); //初始化数据
  },
  methods: {
    ...mapActions(["initDate"])
  }
};
</script>

<style>
#app {
  height: 100%;
}

.init-loding {
  position: absolute;
  left: 50%;
  top: 50%;
  /* transform: translate(-50%, -50%); */
}
</style>
