import Vue from "vue";
import moment from "moment";
import { Message, Modal } from "ant-design-vue";
import localforage from "localforage";
const db = localforage.createInstance({
  name: "logs"
});
//获取一个时间点属于那一天
const getDay = time => {
  const day = new Date(time);
  const date = day.getDate();
  const month = day.getMonth();
  const year = day.getFullYear();

  return new Date(`${year}/${month + 1}/${date}`).getTime();
};

//判断两个块是否相等
const isEqual = (block1, block2) => {
  if (!block1 || !block2) return false;

  const key1 = block1.category === block2.category;
  const key2 = block1.event === block2.event;
  const key3 = block1.description === block2.description;

  return key1 && key2 && key3;
};

const state = {
  originDate: new Date("2016/01/01").getTime(), //起始日期
  finalDate: new Date(`${new Date().getFullYear() + 1}/01/01`).getTime(), //结束日期，下一年
  days: [], //所有的日期，从originDate到finalDate
  visibleDays: [], //可见日期
  scrollToDay: 0, //哨兵变量，改变后更新滚动条
  cardWidth: 241.7, //卡片宽度
  blocks: {}, //渲染的块
  inMul: false, //多选标记
  selectedBlocks: {}, //已选择的块
  selectedDays: {}, //已选择的日期
  startBlock: null, //多选起始块
  endBlock: null, //多选结束块
  logs: {} //运行时log
};

const getters = {
  //- Block view state
  scrollToDay(state) {
    return state.scrollToDay;
  },
  visibleDays(state) {
    return state.visibleDays;
  },
  days(state) {
    return state.days;
  },
  originDate(state) {
    return state.originDate;
  },
  finalDate(state) {
    return state.finalDate;
  },
  cardWidth(state) {
    return state.cardWidth;
  },
  //- Block run time
  inMul(state) {
    return state.inMul;
  },
  logs(state) {
    return state.logs;
  },
  selectStart(state) {
    return state.startBlock;
  },
  selectEnd(state) {
    return state.endBlock;
  },
  blocksState(state) {
    return state.blocks;
  },
  selectedBlocks(state) {
    return state.selectedBlocks;
  },
  hasChosen() {
    return Object.keys(state.selectedBlocks).length !== 0;
  },
  selectedDays(state) {
    return state.selectedDays;
  }
};

const mutations = {
  setLogsByFile(state, logs) {
    state.logs = logs;
  },
  //- Block view state
  changeScroll(state, day) {
    state.scrollToDay = day;
  },
  //初始化日期数组
  initDays(state) {
    const days = [];
    for (
      let day = state.originDate;
      day < state.finalDate;
      day += 24 * 60 * 60 * 1000
    ) {
      days.push(day);
    }

    state.days = days;
  },
  //可视化可见日期数组
  initVisibleDays(
    state,
    day = new Date(moment().format("YYYY/MM/DD")).getTime()
  ) {
    const head = (day - state.originDate) / (24 * 60 * 60 * 1000);
    state.visibleDays[0] = state.days.slice(head, head + 1);
    this.commit("changeScroll", day); //触发更新
  },
  //更新可见日期数组
  updatedVisibleDays(state, { scrollLeft, clientWidth }) {
    const scrollRight = scrollLeft + clientWidth;
    const head = Math.floor(scrollLeft / state.cardWidth);
    const tail = Math.ceil(scrollRight / state.cardWidth);
    state.visibleDays = state.days.slice(head, tail);
  },
  //设置多选状态
  setMulState(state, value) {
    state.inMul = value;
  },
  //- Block run time
  setLog(state, { day, newLog }) {
    Vue.set(state.logs, day, newLog);
  },
  //添加运行时块状态
  addBlocksState(state, data) {
    const old = state.blocks;
    Vue.set(state, "blocks", { ...data, ...old });
  },
  //设置运行时块状态
  setBlockState(state, { block, record }) {
    state.blocks[block] = record;
  },
  //通过时间递增当前日期的选择块数量
  incSelDayByTime(state, time) {
    const day = getDay(parseInt(time));
    let data = state.selectedDays[day] + 1;
    if (isNaN(data)) {
      data = 1;
    }
    Vue.set(state.selectedDays, day, data);
  },
  //通过时间递减当前日期的选择块数量
  decSelDayByTime(state, time) {
    const day = getDay(parseInt(time));

    let data = state.selectedDays[day] - 1;

    if (isNaN(data)) {
      data = 0;
    }

    Vue.set(state.selectedDays, day, data);
  },
  //添加选择的块
  addSelBlock(state, time) {
    document.getElementById(time).classList.add("chosen");
    Vue.set(state.selectedBlocks, time, true);
  },
  //删除选择的块
  delSelBlock(state, time) {
    const el = document.getElementById(time);
    if (el) {
      //防止跨区间选择报错
      el.classList.remove("chosen");
    }
    delete state.selectedBlocks[time];
    Vue.set(state, "selectedBlocks", {
      ...state.selectedBlocks
    });
  },
  //触发logs更新
  triggeLogs(state) {
    Vue.set(state, "logs", { ...state.logs });
  },
  //设置多选的起始块
  setMultiSelStart(state, time) {
    state.startBlock = time;
  },
  //设置多选的结束块
  setMultiSelEnd(state, time) {
    state.endBlock = time;
  },
  //初始化多选状态
  initMultiSel(state) {
    //长按进入多选状态后触发
    document.getElementById(state.startBlock).classList.add("chosen");
    const isChosen = state.selectedBlocks[state.startBlock]; //获取选择状态，判断是否标记日期
    if (!isChosen) this.commit("incSelDayByTime", state.startBlock);
    state.endBlock = state.startBlock;
    state.selectedBlocks[state.startBlock] = true;
    state.inMul = true;
  },
  //取消多选状态
  cancelMultiSel(state) {
    state.startBlock = null;
    state.endBlock = null;
    state.inMul = false;
  }
};

const actions = {
  async setLogsByFile({ commit }, logs) {
    //1.清除旧数据（先不做）

    //2.设置记录
    for (let day in logs) {
      db.setItem(day, logs[day]);
    }
    commit("setLogsByFile", logs);
  },
  //- Block run time
  //更新选择区间
  async updatedMulInterval({ commit, state, rootState }, curTime) {
    if (!rootState.blocks.inMul) return; //如果未处于多选状态中则结束运行

    const size = parseInt(rootState.setting.setting.block.size) * 60 * 1000;
    const originalStart = state.startBlock;
    let top = state.startBlock; //旧区间的头
    let tail = state.endBlock; //旧区间的尾
    let newTop = top; //新区间的头
    let newTail = curTime; //新区间的尾
    let newOffset = 0; //新区间偏移(收缩)量

    //确保头尾方向正确
    if (top > tail) [top, tail] = [tail, top];
    if (newTop > newTail) [newTop, newTail] = [newTail, newTop];

    //遍历旧区间
    for (let time = top; time <= tail; time += size) {
      if (newTop <= time && time <= newTail) {
        //如果旧区间该时间点与新区间部分重叠，则收缩新区间
        newOffset += size;
      } else {
        //如果旧区间该时间点不在新区间中，则取消该时间点的选择状态
        commit("delSelBlock", time);
        commit("decSelDayByTime", time);
      }
    }

    //判断多选方向选择新区间的收缩部位
    if (originalStart <= curTime) {
      newTop += newOffset;
    } else {
      newTail -= newOffset;
    }

    //遍历新区间，设置时间点的选择状态
    for (let time = newTop; time <= newTail; time += size) {
      commit("addSelBlock", time);
      commit("incSelDayByTime", time);
    }

    //更新选择区间
    commit("setMultiSelEnd", curTime);
  },
  //取消选择
  async unselect({ commit, state }) {
    for (let key in state.selectedBlocks) {
      commit("delSelBlock", key);
      commit("decSelDayByTime", key);
    }
  },
  //删除记录
  async delRecord({ dispatch }) {
    dispatch("setRecord", { category: "" });
  },
  //渲染一天的块
  async renderDay({ rootState, commit }, day) {
    const log = await db.getItem(day.toString());
    if (!log || log.length === 0) {
      db.removeItem(day.toString()); //删除空记录
      return;
    }
    state.selectedDays[day] = 0;
    const result = {};

    //获取第一条记录
    let record = log.shift();
    let recordStart = record.time;
    let recordEnd = recordStart + record.duration * 60 * 1000;
    const blockSize =
      parseInt(rootState.setting.setting.block.size) * 60 * 1000;
    const daySize = 24 * 60 * 60 * 1000;
    //遍历当天的每一块
    for (
      let timePoint = day;
      timePoint < day + daySize;
      timePoint += blockSize
    ) {
      //判断当前块属于哪一个哪一条记录
      //TimeLine 判断重叠
      const lineStart = Math.max(recordStart, timePoint);
      const lineEnd = Math.min(recordEnd, timePoint + blockSize);

      if (lineEnd - lineStart > 0) {
        //如果存在记录则设置该块的记录,无记录就不设置
        result[timePoint] = {
          event: record.event,
          category: record.category,
          description: record.description
        };
      } else if (timePoint >= recordEnd) {
        //当前块的时间超出当前记录区间：取一条记录，重新判断
        if (log.length === 0) break;
        record = log.shift();
        recordStart = record.time;
        recordEnd = recordStart + record.duration * 60 * 1000;
        timePoint -= blockSize; //还原值，和for中的+抵消
      }
    }

    commit("addBlocksState", result);
  },
  async getLogByDay(n, day) {
    return db.getItem(day.toString());
  },
  hasRecord({ state, getters }) {
    const selectedBlocks = state.selectedBlocks;
    const blocks = state.blocks;
    for (let block in selectedBlocks) {
      if (
        blocks[block] &&
        blocks[block].category !== "" &&
        getters.categorys[blocks[block].category]
      )
        return true;
    }

    return false;
  },
  //设置记录
  async setRecord({ commit, dispatch, getters, state }, record) {
    //0. 判断是否选择块
    if (!getters.hasChosen) {
      return Message.error("未选择块");
    }
    //1.获取选择的分类、事件
    const { category, event, description } = record;
    const filterRecord = { category, event, description };

    //- 实际设置块记录的函数，为了在if中使用
    const setRecord = () => {
      //2.设置事件
      const blocks = state.selectedBlocks;
      for (let block in blocks) {
        //设置分类、事件、项目
        commit("setBlockState", {
          block,
          record: filterRecord
        });
      }

      //3. 解析块为记录
      dispatch("parseBlocks");

      //4. 取消选择
      dispatch("unselect");
    };

    //- 判断是否删除记录
    const isDel = category === ""; //标记是否为删除操作
    const hasRecord = await dispatch("hasRecord"); //判断选择的块中是否有已设置记录的块

    if (hasRecord) {
      Modal.confirm({
        title: "所选择的块中有已设置记录的块",
        content: `确定${isDel ? "删除" : "覆盖"}吗`,
        okText: isDel ? "删除" : "覆盖",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          setRecord();
        },
        onCancel() {
          dispatch("unselect");
        }
      });
    } else {
      setRecord();
    }
  },
  //设置描述
  async setDes({ commit, dispatch, state }, description) {
    //1.设置块的描述
    const blocks = state.selectedBlocks;
    for (let block in blocks) {
      //设置分类、事件、项目
      const { category, event } = state.blocks[block];
      const record = { category, event, description };
      commit("setBlockState", { block, record });
    }

    //2. 解析块为记录
    dispatch("parseBlocks");

    //3. 取消选择
    dispatch("unselect");
  },
  //解析块为log
  async parseBlocks({ dispatch, state, commit }) {
    //1. 获取被选择日期
    const days = state.selectedDays;

    //2. 解析并提交记录
    for (let day in days) {
      //2.1 过滤未被选中的区域
      if (days[day] < 1) continue;
      // console.log(moment(parseInt(day)).format("YYYY-MM-DD"));
      //2.2 解析被选中日期为log
      const log = await dispatch("parseDay", day);

      //2.3 保存log
      const newLog = await db.setItem(day, log);

      //2.4 设置运行时logs
      commit("setLog", {
        day,
        newLog
      });
    }

    //3. 触发Logs更新
    commit("triggeLogs");
  },
  //解析一天的块
  async parseDay({ state, rootState }, day) {
    const startTime = parseInt(day); //开始时间，当天 00:00
    const endTime = startTime + 24 * 60 * 60 * 1000; //结束时间,下一天 00:00
    const size = parseInt(rootState.setting.setting.block.size); //块大小
    const blocks = state.blocks; //获取运行时块状态
    const log = []; //初始化log
    class Record {
      constructor(time = 0, duration = 0) {
        this.time = time;
        this.category = "";
        this.event = "";
        this.description = "";
        this.duration = duration;
      }
    }

    let record = new Record();

    let prevPtr = null; //指针，指向前一个块
    for (let time = startTime; time < endTime; time += size * 60 * 1000) {
      const block = blocks[time];
      const prevBlock = blocks[prevPtr];
      if (isEqual(prevBlock, block)) {
        //叠加时长
        record.duration += size;
      } else {
        //保存当前数据，推入记录
        if (record.category !== "") log.push(record);
        //初始化记录
        record = new Record(time, size);

        if (block) {
          const { category, event, description } = block;
          record = { ...record, category, event, description };
        }
      }

      prevPtr = time;
    }

    //处理末尾块的记录没能推入的问题
    if (record.category !== "") log.push(record);

    return log;
  },
  async getLogsAll() {
    const logs = {};
    await db.iterate(function(value, key) {
      logs[key] = value;
    });
    return logs;
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
