import Vue from "vue";
import Vuex from "vuex";
import categorys from "./modules/categorys";
import events from "./modules/events";
import setting from "./modules/setting";
import blocks from "./modules/blocks";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loading: true
  },
  getters: {
    loading(state) {
      return state.loading;
    }
  },
  mutations: {
    onLoaded(state) {
      state.loading = false;
    }
  },
  actions: {
    //读取数据
    initDate({ dispatch, commit }) {
      const promise = [];
      ["initCategorys", "initEvents", "initSettings"].forEach(action =>
        promise.push(dispatch(action))
      );

      Promise.all(promise).then(() => {
        commit("onLoaded");
      });
    },
    async firstLoad({ dispatch }) {
      //0. 设置初始加载标记
      dispatch("setOption", { key: "notFirst", option: true });

      const mapCategorys = {};
      //1. 生成分类
      const categorys = {
        高效: {
          color: "#4E83FD",
          description:
            "任何形式的学习、自己喜欢的工作、能够提升自己能力并能带来成就感的事情。",
          shortcut: ["学习", "健身", "慢跑"]
        },
        工作: {
          color: "#935AF6",
          description: "不喜欢但必须要做的工作，（我认为家务劳动也是这一类）。",
          shortcut: []
        },
        拖延: {
          color: "#F76964",
          description:
            "拖延时间，比如因无聊刷微博、朋友圈、剧等，找借口拖延迟迟不开始行动，凡是在这个时间段里你想不起来做过什么的，都算在“无效拖延”一类。",
          shortcut: ["刷微博", "刷朋友圈", "玩游戏", "看剧"]
        },
        休息: {
          color: "#34C724",
          description:
            "让身心感到放松的时间，比如适当的小睡、一个人吃饭、听歌、看电影等",
          shortcut: ["睡觉", "吃饭"]
        },
        娱乐: {
          color: "#FFC60A",
          description:
            "无负罪感的玩，让你开心的事情，比如：朋友聚会、看电影、玩游戏（无负罪感的玩）。",
          shortcut: ["看剧", "看电影", "玩游戏"]
        }
      };

      for (let name in categorys) {
        mapCategorys[name] = (
          await dispatch("createCategory", {
            name,
            color: categorys[name].color,
            description: categorys[name].description
          })
        ).id;
      }

      //2. 生成事件
      const mapEvents = {};
      const events = {
        睡觉: { abbr: "Sleep", icon: "🛌" },
        吃饭: { abbr: "Eating", icon: "🍵" },
        阅读: { abbr: "Reading", icon: "📖" },
        学习: { abbr: "Learning", icon: "👨‍🎓" },
        健身: { abbr: "Fitness", icon: "🏋" },
        慢跑: { abbr: "Jog", icon: "🏃" },
        看剧: { abbr: "TV", icon: "📺" },
        看电影: { abbr: "Movie", icon: "🎥" },
        玩游戏: { abbr: "Game", icon: "🎮" },
        刷微博: { abbr: "Weibo", icon: "📱" },
        刷朋友圈: { abbr: "PYQ", icon: "📱" }
      };

      for (let name in events) {
        const { abbr, icon } = events[name];
        mapEvents[name] = (
          await dispatch("createEvent", {
            name,
            abbr,
            icon
          })
        ).id;
      }

      //3. 生成快捷方式
      for (let ca in categorys) {
        const shortcut = [];
        for (let ev of categorys[ca].shortcut) {
          shortcut.push(mapEvents[ev]);
        }
        dispatch("setShortcut", { id: mapCategorys[ca], shortcut });
      }

      //4. 收藏事件
      for (let name in mapCategorys) {
        dispatch("toggleStar", mapCategorys[name]);
      }
    }
  },
  modules: {
    categorys,
    events,
    setting,
    blocks
  }
});
