import uniqid from "uniqid";
import localforage from "localforage";
import Vue from "vue";
import pinyin from "tiny-pinyin";
const db = localforage.createInstance({
  name: "events"
});

const state = {
  events: {}
};

const getters = {
  //R:获取事件
  events(state) {
    return state.events;
  }
};

const mutations = {
  //初始化事件
  initEvents(state, events) {
    state.events = events;
  },
  //创建事件
  createEvent(state, data) {
    state.events[data.id] = data;
    Vue.set(state, "events", { ...state.events });
  },
  //更新事件
  updatedEvent(state, data) {
    state.events[data.id] = data;
    Vue.set(state, "events", { ...state.events });
  },
  //删除事件
  deleteEvent(state, id) {
    delete state.events[id];
    Vue.set(state, "events", { ...state.events });
  },
  //切换star状态
  toggleStarEvent(state, id) {
    state.events[id].star = !state.events[id].star;
    Vue.set(state, "events", { ...state.events });
  },
  //切换归档状态
  toggleArchiveEvent(state, id) {
    state.events[id].active = !state.events[id].active;
    Vue.set(state, "events", { ...state.events });
  }
};

const actions = {
  setEventsByFile(n, events) {
    //1. 清除当前所有数据（先不实现试试效果）

    //2. 读取文件数据
    for (let id in events) {
      db.setItem(id, events[id]);
    }
    // commit("initEvents", events);
  },
  async initEvents({ commit }) {
    const events = {};
    await db.iterate(function(value, key) {
      events[key] = value;
    });
    commit("initEvents", events);
  },
  //C:添加新的事件
  async createEvent({ commit }, data) {
    const id = uniqid("events-");
    let { abbr, description, name, icon } = data;

    if (abbr === undefined || abbr.trim() === "") abbr = name;
    if (description === undefined || description.trim() === "")
      description = "还没有描述...";

    const createEvent = {
      id,
      abbr,
      description,
      name,
      icon,
      star: false,
      active: true
    };
    try {
      const item = await db.setItem(id, createEvent);
      commit("createEvent", item);
      return item;
    } catch (err) {
      console.log(err);
    }
  },

  //U：更新事件
  async updatedEvent({ commit }, data) {
    let { abbr, description, name, icon, id } = data;

    if (abbr === undefined || abbr.trim() === "") abbr = name;
    if (description === undefined || description.trim() === "")
      description = "还没有描述...";

    let item = await db.getItem(id);
    item = { ...item, abbr, description, name, icon, id };

    try {
      const newItem = await db.setItem(id, item);
      commit("updatedEvent", newItem);
    } catch (err) {
      console.log(err);
    }
  },

  //D: 删除分类
  async deleteEvent({ commit }, id) {
    try {
      await db.removeItem(id);
      commit("deleteEvent", id);
    } catch (err) {
      console.log(err);
    }
  },

  //切换Star状态
  async toggleStarEvent({ commit }, id) {
    try {
      const item = await db.getItem(id);
      item.star = !item.star;
      await db.setItem(id, item);
      commit("toggleStarEvent", id);
      // console.log(item.star)
    } catch (err) {
      console.log(err);
    }
  },

  //切换active状态
  async toggleArchiveEvent({ commit }, id) {
    try {
      const item = await db.getItem(id);
      item.active = !item.active;
      await db.setItem(id, item);
      commit("toggleArchiveEvent", id);
    } catch (err) {
      console.log(err);
    }
  },

  //获取首字母
  async getInitial(n, str) {
    if (!str || str.length === 0) return "";
    const result = [];
    const parse = pinyin.parse(str);
    for (let key in parse) {
      result.push(parse[key].target[0]);
    }
    return result.join("");
    // console.log("首字母", result.join(""));
  },
  async getPinyin(n, str) {
    if (!str || str.length === 0) return "";
    return pinyin.convertToPinyin(str);
  }

  //TODO 设置默认事件
};

export default {
  state,
  getters,
  mutations,
  actions
};
