import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    redirect: { path: "/block/" }
    // component: Home
  },
  {
    path: "/block",
    name: "block",
    component: () =>
      import(/* webpackChunkName: "block" */ "../views/page-block")
  },
  {
    path: "/statistics",
    name: "statistics",
    component: () =>
      import(/* webpackChunkName: "statistics" */ "../views/page-statistics")
  },
  {
    path: "/category",
    name: "category",
    component: () =>
      import(/* webpackChunkName: "category" */ "../views/page-category")
  },
  {
    path: "/event",
    name: "event",
    component: () =>
      import(/* webpackChunkName: "event" */ "../views/page-event")
  },
  {
    path: "/setting",
    name: "setting",
    redirect: { name: "setting-time" },
    component: () =>
      import(/* webpackChunkName: "setting" */ "../views/page-setting"),
    children: [
      {
        path: "time",
        name: "setting-time",
        component: () =>
          import(
            /* webpackChunkName: "setting" */ "../views/page-setting/pages/setting-time.vue"
          )
      },
      {
        path: "predilection",
        name: "setting-predilection",
        component: () =>
          import(
            /* webpackChunkName: "setting" */ "../views/page-setting/pages/setting-predilection.vue"
          )
      },
      {
        path: "data",
        name: "setting-data",
        component: () =>
          import(
            /* webpackChunkName: "setting" */ "../views/page-setting/pages/setting-data.vue"
          )
      }
    ]
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ "../views/About")
  },
  {
    path: "*",
    redirect: { name: "block" }
    // component: Homes
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

export default router;
