import localforage from "localforage";
import Vue from "vue";
const db = localforage.createInstance({
  name: "setting"
});

//自定义前缀
const state = {
  setting: {
    block: {
      size: 30,
      showField: ["category.name", "event.abbr"]
    },
    notFirst: false
  }
};

const getters = {
  setting(state) {
    return state.setting;
  }
};

const mutations = {
  initSettings(state, settings) {
    for (let key in settings) {
      Vue.set(state.setting, key, settings[key]);
    }
  },
  setOption(state, { key, newOption }) {
    Vue.set(state.setting, key, newOption);
  }
};

const actions = {
  setSettingByFile(n, setting) {
    //1. 清除当前所有数据（先不实现试试效果）

    //2. 读取文件数据
    for (let key in setting) {
      db.setItem(key, setting[key]);
    }
  },
  async initSettings({ commit, state, dispatch }) {
    let setting = {};
    await db.iterate(function(value, key) {
      setting[key] = value;
    });

    if (!setting.notFirst) {
      dispatch("firstLoad");
      setting = { ...state.setting, ...setting, notFirst: true };
    }

    if (Object.keys(setting).length === 0) return;
    commit("initSettings", setting);
  },
  async setOption({ commit }, { key, option }) {
    const newOption = await db.setItem(key, option);
    commit("setOption", { key, newOption });
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
