export default () => {
  const colors = [
    "#f14ba9",
    "#f76964",
    "#ff8800",
    "#ffc60a",
    "#34c724",
    "#00d6b9",
    "#4e83fd",
    "#14c0ff",
    "#616ae5",
    "#935af6",
    "#d136d1",
    "#8f959e"
  ];

  return colors[Math.floor(Math.random() * colors.length)];
};
